export default {
    data: () => {
        return {
            nameRules: [
                v => !!v || "Mohon masukkan nama",
                v => (!!v && v.length >= 10) || "Minimal 10 karakter",
            ],
            usernameRules: [v => !!v || "Mohon masukkan username"],
            nisnRules: [
                v => !!v || "Mohon masukkan NISN",
                v => (!!v && v.length == 10) || "NISN adalah berjumlah 10 karakter",
                v => /^([\s\d]+)$/.test(v) || "Mohon masukkan NISN yang valid",
            ],
            phoneNumRules: [
                v => !!v || "Mohon masukkan no telepon",
                v => (!!v && v.length >= 10) || "Minimal 10 karakter",
                v =>
                    /\+?([ -]?\d+)+|\(\d+\)([ -]\d+)/.test(v) ||
                    "Mohon masukkan nomor telepon yang valid",
            ],
            emailRules: [
                v => !!v || "Mohon masukkan email",
                v => /.+@.+/.test(v) || "Alamat email tidak valid",
            ],
            originRules: [(v) => !!v || "Mohon masukkan asal sekolah"],
            passwordRules: [
                v => (v || "").length >= 8 || "Minimal 8 karakter",
                v => /(?=.*[0-9])/.test(v) || "Harus terdapat minimal satu angka",
                v =>
                    /(?=.*[a-z])/.test(v) || "Harus terdapat minimal satu huruf kecil",
                v =>
                    /(?=.*[A-Z])/.test(v) || "Harus terdapat minimal satu huruf besar"
            ]
        };
    }
}