export default {
    methods: {
        getDefaultErrorMessage() {
            return "Terjadi galat. Mohon coba kembali."
        },
        getErrorMessage(jsonError) {
            return jsonError.response.data.message;
        },
        isSessionOver(jsonError) {
            return jsonError.response.data.message.toLowerCase().includes("sesi berakhir");
        }
    }
}