export default class User {
    constructor(name = '', username = '', nisn = '', phone = '', email = '', origin = '', role = 'USER', password = '') {
        this.name = name;
        this.username = username;
        this.nisn = nisn;
        this.phone = phone;
        this.email = email;
        this.origin = origin;
        this.role = role;
        this.password = password;
    }
}